.all{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}
.human{
  width: 600px;
  height: 100vh;
}
.whole_body{
  margin-top: 40px;
  width: 350px;
  height: 500px;
  position:relative;
  position:relative;
}
header{
  background-color:#f7d167;
  width:100px;
  height:100px;
  border-radius:45px 40px 40px 10px;
  border-top:40px solid #000;
}
header .hair{
  background-color:black;
  height:55px;
  width:105px;
  position:relative;
  top:-52px;
  left:0px;
  border-radius:10px 60px 0px 0px;
}
header .nose{
  height:20px;
  width:20px;
  position:relative;
  top:-20px;
  left:-5px;
  border-width:0 0 3px 3px;
  border-color:#e0b231;
  border-style:solid;
  border-radius:5px 0px 12px 10px;
}
header .left-eye{
  position:relative;
  float:left;
  top:-70px;
  left:3px;
  height:20px;
  width:25px;
  background-color:#fff;
  border-radius:12px 12px 8px 8px; 
  border-top: 3px solid #c49205; 
  overflow:hidden;
}
header .right-eye{
  position:relative;
  float:right;
  top:-70px;
  left:-18px;
  height:20px;
  width:25px;
  background-color:#fff;
  border-radius:12px 12px 8px 8px; 
  border-top: 3px solid #c49205; 
  overflow:hidden;
}
header .eye-ball{
  height:15px;
  width:16px;
  position:relative;
  background-color:black;
  top:4px;
  left:0px;
  border-radius:100%;
}
header .eye-ball .cornea{
  height:3px;
  width:2px;
  position:relative;
  background-color:white;
  top:3px;
  left:3px;
  border-radius:100%;
}
header .mouth{
  height: 17px;
  width:45px;
  background-color:#0c0c0c;
  overflow:hidden;
  border-radius: 0px 0px 30px 10px ;
  position:relative;
  left:-1px;
  top:-10px;
}
header .mouth .teeth{
  height:10px;
  width:100px;
  position:relative;
  top:-5px;
  left:-5px;
  background-color:white;
  transform:rotate(-3deg);
}
header .mouth .tongue{
  height:25px;
  width:35px;
  background-color:#f41800;
  border-radius:10px;
  position:relative;
  left:-8px;
  top:0px;
}
.neck{
  width:40px;
  height:25px;
  position:relative;
  top:-10px;
  left:-5px;
  background-color:#f7d167;
  z-index:-1;
}
main{
  width:150px;
  height:170px;
  background-color:blue;
  border-radius: 30px 30px 0 0;
  overflow:hidden;
  padding:0;
  position:relative;
  top:-15px;
  left:-4px;
}
main .collar{
  height:40px;
  width:60px;
  position:relative;
  background-color:#f7d167;
  border-radius:100%;
  top:-25px;
  border: 5px double black;
}
.left-shoulder{
  height:150px;
  width:40px;
  background-color: blue;
  border-radius: 30px 30px 0 0 ;
  position:relative;
  top: -198px;
  left:-85px;
  z-index:-1;
  transform:rotate(40deg);
  border-bottom:5px double black;
}
.right-shoulder{
  height:150px;
  width:40px;
  background-color: blue;
  border-radius: 30px 30px 0 0 ;
  position:relative;
  z-index:-1;
  left:80px;
  top:-353px;
  border-bottom:5px double black;
  transform:rotate(-40deg);
}
.left{
  height:145px;
  width:50px;
  background-color:#0c0c0c;
  display:inline-block;
  position:relative;
  top:-326px;
  left:-20px;
  border-radius: 0 0 5px 5px;
}
.right{
  height:145px;
  width:50px;
  background-color:#0c0c0c;
  display:inline-block;
  position:relative;
  top:-326px;
  left:10px;
  border-radius: 0 0 5px 5px;
}
.buttons{
  width: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
button{
  width: 150px;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, 0.678);
  border-radius: 10px;
  color: black;
  font-size: 1.1em;
  background-color: gray;
  box-shadow: 2px 2px 2px black;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}